import React from "react";
import intl from "react-intl-universal";
import { setInputValue } from "utils/helpers";
import withApiService from "HOC/withApiService";
import compose from "utils/compose";
import Form from "components/Form";
import Label from "components/Label";
import TextField from "components/TextField";
import Button from "components/Button";
import Image from "components/Image";
import Box from "components/Box";
import SubTitle from "components/SubTitle";
import logoMain from "images/logo-main.png";
import logoSecondary from "images/logo-secondary.png";
import withSessionContext from "HOC/withSessionContext";
import "./Login.css";

const token1 =
  "eyJhbGciOiJIUzI1NiJ9.eyJVU0VSIjoie1wic3RhdHVzXCI6MCxcImdlbmRlclwiOjAsXCJsb2NhdGlvblwiOjAsXCJiYWRQYXNzd29yZENvdW50XCI6MCxcInVzZXJJRFwiOjAsXCJsYXN0TG9nb25UaW1lXCI6MCxcImJhZFBhc3N3b3JkVGltZVwiOjAsXCJyb2xlc1wiOltcIlJPTEVfMVwiLFwiUk9MRV8yXCIsXCJST0xFXzNcIl19In0.8jBqTJ5DppDzjvCpbEPEVy5HwfqamD5pQdWHdwPjofo";
const token2 =
  "eyJhbGciOiJIUzI1NiJ9.eyJYQkNfU1RBVEUiOiJ7XCJ1c2VyXCI6e1wic3RhdHVzXCI6MCxcImdlbmRlclwiOjAsXCJsb2NhdGlvblwiOjAsXCJiYWRQYXNzd29yZENvdW50XCI6MCxcInVzZXJJRFwiOjAsXCJsYXN0TG9nb25UaW1lXCI6MCxcImJhZFBhc3N3b3JkVGltZVwiOjAsXCJyb2xlc1wiOltcIlJPTEVfMVwiLFwiUk9MRV8yXCIsXCJST0xFXzNcIl19fSJ9.0_kzRyyf85NSDXVFrtxInoyeYEtODuBA9QIVLccbSvM";

class Login extends React.Component {
  state = {
    currentState: "idle",
    isChangePassword: false
  };
  _setState = this.setState;
  usernameEl = React.createRef();
  passwordEl = React.createRef();
  newPasswordEl = React.createRef();
  confirmNewPasswordEl = React.createRef();

  componentWillUnmount() {
    this._setState = null;
  }

  getXbcToken = () => {
    const {
      apiService: { post, urls, parseJwt },
      sessionContext
    } = this.props;

    // const res = await post({ url: urls.postLogin });
    return Promise.resolve()
      .then(
        () => new Promise(resolve => setTimeout(() => resolve(token2), 250))
      )
      .then(jwt => {
        sessionStorage.setItem("jwt2", jwt);
        let parsed = JSON.parse(parseJwt(jwt).XBC_STATE);
        sessionContext._setState({
          ...parsed,
          jwt2: jwt
        });
      });
  };

  getUserObject = () => {
    const {
      apiService: { post, urls, parseJwt },
      sessionContext
    } = this.props;

    const {
      current: { el: usernameEl }
    } = this.usernameEl;
    const {
      current: { el: passwordEl }
    } = this.passwordEl;

    const credentials = {
      username: usernameEl.value,
      password: passwordEl.value
    };
    // const res = await post({ url: urls.login, data: credentials });
    return Promise.resolve()
      .then(
        () => new Promise(resolve => setTimeout(() => resolve(token1), 250))
      )
      .then(jwt => {
        setInputValue(usernameEl, "");
        setInputValue(passwordEl, "");

        sessionStorage.setItem("jwt1", jwt);
        let parsed = JSON.parse(parseJwt(jwt).USER);
        sessionContext._setState({
          ...parsed,
          jwt1: jwt
        });
      });
  };

  authenticate = async () => {
    this._setState &&
      this._setState(
        state => ({
          currentState: "loading"
        }),
        async () => {
          try {
            await this.getUserObject();
            await this.getXbcToken();
          } catch (e) {
            this.props.sessionContext.addNotification({ message: e });
            this._setState &&
              this._setState({
                currentState: "idle"
              });
          }
        }
      );
  };

  toggleChangePasswordFields = () => {
    this.setState(state => ({
      isChangePassword: !state.isChangePassword
    }));
  };

  changePassword = async () => {
    console.log("changing password");
  };

  render() {
    const { isChangePassword, currentState } = this.state;

    return (
      <div id="Login">
        <div className="login-container">
          <div style={{ textAlign: "center", paddingTop: "5em" }}>
            <Image src={logoMain} />
            <Image style={{ width: "260px" }} src={logoSecondary} />
          </div>
          <br />
          <Box className="login-box">
            <SubTitle style={{ color: "white", textAlign: "center" }} is="4">
              {intl.get("saudiArabia")}
            </SubTitle>
            <SubTitle style={{ color: "white", textAlign: "center" }} is="4">
              {intl.get("ministryNIC")}
            </SubTitle>
            <Form style={{ maxWidth: "350px", margin: "0 auto" }}>
              <TextField
                ref={this.usernameEl}
                disabled={currentState === "loading"}
                dataTest="login-username-field"
                label={
                  <Label style={{ color: "white", fontWeight: "400" }}>
                    {intl.get("username")}
                  </Label>
                }
              />
              <TextField
                ref={this.passwordEl}
                type="password"
                disabled={currentState === "loading"}
                dataTest="login-password-field"
                label={
                  <Label style={{ color: "white", fontWeight: "400" }}>
                    {intl.get("password")}
                  </Label>
                }
              />
              {isChangePassword && (
                <React.Fragment>
                  <TextField
                    ref={this.newPasswordEl}
                    type="password"
                    dataTest="login-new-password-field"
                    disabled={currentState === "loading"}
                    label={
                      <Label style={{ color: "white", fontWeight: "400" }}>
                        {intl.get("newPassword")}
                      </Label>
                    }
                  />
                  <TextField
                    ref={this.confirmNewPasswordEl}
                    type="password"
                    disabled={currentState === "loading"}
                    dataTest="login-confirm-new-password-field"
                    label={
                      <Label style={{ color: "white", fontWeight: "400" }}>
                        {intl.get("confirmNewPassword")}
                      </Label>
                    }
                  />
                </React.Fragment>
              )}
              <a
                style={{ color: "white" }}
                onClick={this.toggleChangePasswordFields}
              >
                {intl.get("changePassword")}
              </a>
              <div style={{ display: "flex", justifyContent: "flex-end" }}>
                {isChangePassword ? (
                  <Button
                    className={
                      "is-primary" +
                      (currentState === "loading" ? " is-loading" : "")
                    }
                    onClick={this.changePassword}
                    disabled={currentState === "loading"}
                    dataTest="change-password-btn"
                  >
                    {intl.get("changePassword")}
                  </Button>
                ) : (
                  <Button
                    className={
                      "is-primary" +
                      (currentState === "loading" ? " is-loading" : "")
                    }
                    onClick={this.authenticate}
                    disabled={currentState === "loading"}
                    dataTest="login-btn"
                  >
                    {intl.get("login")}
                  </Button>
                )}
              </div>
            </Form>
          </Box>
        </div>
        <div className="page-code">
          <span>LGOO</span>
        </div>
      </div>
    );
  }
}
export default compose(
  withApiService,
  withSessionContext
)(Login);

export { Login };



